import React from "react";

const About = React.lazy(() => import("./views/dashboard/Dashboard"));
const Contact = React.lazy(() => import("./views/contact/contact"));
const Donnees = React.lazy(() => import("./views/donnees/donneesAntoine"));
const Devices = React.lazy(() => import("./views/devices/devices"));
const ViewDevice = React.lazy(() => import("./views/devices/viewDevice"));
const Users = React.lazy(() => import("./views/users/Users"));
const User = React.lazy(() => import("./views/users/User"));
const Dashboard = React.lazy(() => import("./views/bigPicture/BigPicture"));

const routes = [
  { path: "/", exact: true, name: "Home" },
  { path: "/home", name: "About", component: About },
  { path: "/data", name: "Dashboard", component: Dashboard },
  { path: "/contact", name: "Contact", component: Contact },
  { path: "/batterie", name: "Données", component: ViewDevice },
  { path: "/panneauSolaire", name: "Données", component: ViewDevice },
  { path: "/mppt", name: "mppt", component: ViewDevice },
  { path: "/Sommaire", name: "Sommaire", component: Donnees },
  { path: "/chargeur", name: "Données", component: ViewDevice },
  { path: "/sortie1", name: "Données", component: ViewDevice },
  { path: "/onduleur", name: "Données", component: ViewDevice },
  { path: "/sortie2", name: "Graphiques", component: ViewDevice },
  { path: "/devices", name: "Appareils", component: Devices },
  { path: "/users", exact: true, name: "Users", component: Users },
  { path: "/users/:id", exact: true, name: "User Details", component: User },
];

export default routes;
